<template>
  <div class="myprize-wrap">
    <div class="prize-list" v-if="!!prizeInfo.length">
      <div class="list-item-box"
           v-for="(item, index) in prizeInfo"
           :key="index">
        <div class="prize-img-box flex">
          <img v-if="item.ticketImg" :src="img_baseUrl+item.ticketImg" alt="">
          <CornerTag v-if="item.cornerTag" class="type-tag">{{item.cornerTag}}</CornerTag>
        </div>

        <div class="prize-detail-text">
          <div class="prize-name txt-cut">{{item.ticketName}}</div>
          <div class="price-time"><span>中奖时间：</span>{{item.earnTime | formatTime}}</div>
          <div class="price-time"><span>兑奖截止：</span>{{item.expireTime | formatTime}}</div>
          <ButtonM class="prize-tag">天天抽奖</ButtonM>
          <ButtonM v-if="item.useStatus==='0'"
                   class="btn-get"
                   @click="toGetPrize(item)">点击领取</ButtonM>
          <ButtonM v-else class="btn-get" @click="toCheckPrize(item)">查看</ButtonM>
        </div>
      </div>

<!--      <div class="tip-bottom">领奖截止：从奖品中奖时间顺延  30个自然日之内</div>-->
    </div>
    <NoRecord v-if="noRecord">你还没有中奖记录呦～</NoRecord>
  </div>
</template>

<script>
  import ButtonM from '../../components/ButtonM/ButtonM';
  import CornerTag from './conponents/CornerTag/CornerTag';
  import NoRecord from './conponents/NoRecord/NoRecord';
  import {myPrizeRecord} from "../../api/my";
  import {isToday} from "../../utils/tools";
  import {baseUrl} from "../../utils/request";
  import {clog} from "../../api/punchin";
  import {title_tip} from "../../utils/publictip";
  export default {
    name: "my-prize",
    components: {ButtonM, CornerTag, NoRecord},
    data() {
      return {
        prizeInfo: [
          // {
            // infoKey: "记录主键",
            // earnTime: "中奖时间",
            // ticketName: "奖项名称",
            // ticketFullName: "Callaway卡拉威高尔夫球杆",
            // ticketImg: "奖项列表图片",
            // marketPrice: "市场参考价格",
            // useStatus: "领奖状态0:未领取、1:已领取",
            // userName: "领奖人姓名",
            // phoneNum: "领奖人手机号",
            // exchangeChannel: "大奖渠道：0天天抽奖、1转盘抽奖"
          // }
        ],
        img_baseUrl: baseUrl.imgURL,
        noRecord: false
      }
    },
    filters: {
      formatTime(val) {
        return val.split(' ')[0].replace(/\-/g,'.')
      }
    },
    methods: {
      handleInfoList(list) {
        list.map(item => {
          item.earnTime = item.earnTime.split(' ')[0]
          if(isToday(new Date(item.earnTime))) item.cornerTag = '当期'
          const expireTime = item.expireTime.substring(0, 19)
          if(+new Date() > +new Date(expireTime)) item.cornerTag = '已过期'
          if(item.useStatus!=='0') item.cornerTag = '已领取'
        })
      },
      toCheckPrize(item) {
        item = Object.assign({}, item)
        // 后端字段grandPrizeType换成了prizeType
        item.grandPrizeType = item.prizeType
        item.type = 'check'
        if(item.grandPrizeType.substr(0,1) === 'P' || item.grandPrizeType.substr(0,1) === 'p') {
          item.prizeType = 'real'
          this.$router.push({name: 'addressInput', params: item})
        } else {
          item.prizeType = 'virtual'
          this.$router.push({name: 'virtualCard', params: item})
        }
        clog('天天抽奖', ['vjf-h5-log','transTime','openid','好物好生活平',location.href.split('?')[0],'查看奖品','我的奖品页点击查看奖品']);

      },
      toGetPrize(item) {
        if(item.cornerTag === '已过期') {
          title_tip('提 示', '兑奖时间已截止', '我知道了');
          return
        }
        item = Object.assign({}, item)
        item.grandPrizeType = item.prizeType
        item.type = 'get'
        if(item.grandPrizeType.substr(0,1) === 'P' || item.grandPrizeType.substr(0,1) === 'p') {
          item.prizeType = 'real'
        } else {
          item.prizeType = 'virtual'
        }
        this.$router.push({name: 'addressInput', params: item})
        clog('天天抽奖', ['vjf-h5-log','transTime','openid','好物好生活平',location.href.split('?')[0],'领取奖品','我的奖品页点击领奖']);

      }
    },
    created() {
      myPrizeRecord().then(res => {
        const {result, reply} = res
        if(result.businessCode === '0') {
          this.prizeInfo = reply
          this.handleInfoList(this.prizeInfo)
          if(!reply.length) this.noRecord = true
        } else if(result.businessCode === '2' || result.businessCode === '1') {
          this.noRecord = true
        }
      }).catch(e => {
        this.noRecord = true
      })
      clog('天天抽奖', ['vjf-h5-log','transTime','openid','好物好生活平',location.href.split('?')[0],'进入我的奖品页','我的奖品页']);

    }
  }
</script>

