import request from '../utils/request';
import store from '../store/index';
const openid = store.state.userInfo.openid
/******我的******/
/**
 * 领取大奖
 * @param data {...}
 * @returns {AxiosPromise}
 */
export function getPrize(data={}) {
  Object.assign(data, {openid})
  return request({
    url: '/user/savePrize',
    method: 'post',
    data: data
  })
}

/**
 * 我的大奖记录
 * @param data {openid,currentPage,count}
 * @returns {AxiosPromise}
 */
export function myPrizeRecord(data={}) {
  Object.assign(data, {openid,currentPage:1,count:30})
  return request({
    url: '/user/myPrizeRecord',
    method: 'post',
    data: data
  })
}
/**
 * 获取验证码
 * @param data {phonenum}
 * @returns {AxiosPromise}
 */
export function getCaptcha(data) {
  Object.assign(data, {sendtype: '1'})
  return request({
    url: '/user/getCaptcha',
    method: 'post',
    data: data
  })
}



