const openid = store.state.userInfo.openid
/******早安打卡******/
/**
 * 我的打卡情况
 * @param data {openid:openid}
 * @returns {AxiosPromise}
 */
export function myPunchin(data={}) {
  Object.assign(data, {openid})
  return request({
    url: '/punchin/myPunchin',
    method: 'post',
    data: {openid}
  })
}
