// 埋点 
export function clog(type,arr){
  if (process.env.VUE_APP_BUILD_FLAG === 'development') return
  var now = new Date();
  var mon = now.getMonth()+1<10?'0'+(now.getMonth()+1):now.getMonth()+1,
    day = now.getDate()<10?'0'+now.getDate():now.getDate(),
    h = now.getHours()<10?'0'+now.getHours():now.getHours(),
    m = now.getMinutes()<10?'0'+now.getMinutes():now.getMinutes(),
    s = now.getSeconds()<10?'0'+now.getSeconds():now.getSeconds();
  arr[1] = now.getFullYear()+'-'+mon+'-'+day+'T'+h+":"+m+':'+s;
  arr[2] = openid;
  var d=new Date(),
    url = encodeURIComponent(location.pathname),
    msg = encodeURIComponent(arr.join(' ')),
    tst = [d.getHours(), d.getMinutes(), d.getSeconds()].join(':');

  // z.ajxget('/clog?'+type+tst+url+'='+ msg, 5000, function(r) {});
  return request({
    baseURL: baseUrl.vClog,
    url: '/clog?'+type+tst+url+'='+ msg,
    method: 'get'
  })
};
