<template>
  <div class="button-box flex"
       :style="bgUrl"
       @click="click">
    <span>
      <slot></slot>
    </span>
  </div>
</template>

<script>
  export default {
    name: "button-m",
    props: {
      text: {
        type: String,
        default: ''
      },
      bgImg: {
        type: [String, Object],
        default: ''
      }
    },
    methods: {
      click() {
        this.$emit('click')
      }
    },
    computed: {
      bgUrl() {
        return this.bgImg ? {backgroundImage: this.bgImg} : ''
      }
    }
  }
</script>

